mergeI18nTranslations(JSON.parse("{\"ar\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"حذف\",\"edit\":\"تحرير\",\"location\":\"الموقع:\",\"manage\":\"إدارة مجموعة مواعيد %{title}\",\"scheduler_explanation_student_header\":\"ليس لديك أية مجموعات مواعيد للاشتراك فيها\",\"scheduler_explanation_student_paragraph_1\":\"يمكن للمعلمين استخدام هذه الأداة لجدولة بعض الأمور لطلابهم للاشتراك فيها. مثل: ساعات العمل أو الأجزاء الزمنية لمجموعة العرض أو ما شابه.\",\"scheduler_explanation_student_paragraph_2\":\"في الوقت الحالي، لم يقم أي من معلميك بإتاحة أي موعد. سيتم إعلامك إذا أصبحت خانات المواعيد متاحة.\",\"scheduler_explanation_teacher_header\":\"لم تقم بإنشاء أي مجموعات مواعيد، ولا يوجد أي موعد للاشتراك فيه.\",\"scheduler_explanation_teacher_paragraph_1\":\"هذه هي أداة الجدولة. يمكنك استخدامها لإنشاء مجموعات مواعيد لطلابك لتسجيل الاشتراك فيها.\",\"scheduler_explanation_teacher_paragraph_2\":\"على سبيل المثال، لنفترض أنك تريد فحص تعيين ورقة نصف العام مع كل طالب من طلابك في المساق. يمكنك إنشاء مجموعة مواعيد جديدة، وتحدد التواريخ والأوقات التي تكون متاحًا فيها، وتقوم بتقسيم تلك الأوقات إلى مجموعات زمنية مقدار كل منها 15 دقيقة وتسمح بطالب واحد فقط لكل مجموعة زمنية.\",\"scheduler_explanation_teacher_paragraph_3\":\"ثم يتم إخطار الطلاب وسوف يرون أنهم بحاجة إلى تسجيل الاشتراك في واحدة من هذه المجموعات الزمنية. وسيتم عرض تقويم أمامهم تظهر به المجموعات الزمنية متراكبة فوق كل شيء موجود في تقاويمهم الأخرى حتى يتسنى لهم اختيار الوقت المناسب لهم.\",\"scheduler_explanation_teacher_paragraph_4\":\"* يمكنك البدء الآن بالنقر فوق زر \\\"إنشاء مجموعة مواعيد\\\" الموجود على اليمين.\",\"signed_up_for\":\"*مجموعة مواعيد %{title}* التي اشتركت فيها\",\"x_groups_have_signed_up\":\"%{n_participant_count} مجموعات قامت بالتسجيل\",\"x_people_have_signed_up\":\"%{n_participant_count} أشخاص قاموا بالتسجيل\"}},\"message_groups_who_1ba736f\":\"إرسال رسالة للمجموعات التي...\",\"message_students_who_369b8488\":\"إرسال رسائل للطلاب الذين...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*مجموعة مواعيد %{title}* التي يمكنك **الاشتراك** فيها.\"},\"cy\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Dileu\",\"edit\":\"Golygu\",\"location\":\"Lleoliad:\",\"manage\":\"Rheoli’r grŵp apwyntiadau %{title}\",\"scheduler_explanation_student_header\":\"Does gennych chi ddim grwpiau o apwyntiadau i gofrestru ar eu cyfer\",\"scheduler_explanation_student_paragraph_1\":\"Gall athrawon ddefnyddio’r adnodd hwn i drefnu pethau y gall myfyrwyr gofrestru ar eu cyfer, e.e. oriau swyddfa, slotiau amser ar gyfer cyflwyniadau grŵp ac ati.\",\"scheduler_explanation_student_paragraph_2\":\"Ar hyn o bryd, does dim un o’ch athrawon wedi darparu unrhyw apwyntiadau. Byddwch chi’n cael gwybod os bydd slotiau ar gyfer apwyntiadau yn dod ar gael.\",\"scheduler_explanation_teacher_header\":\"Dydych chi ddim wedi creu unrhyw grwpiau apwyntiadau, a does dim un y gallwch chi gofrestru ar ei gyfer\",\"scheduler_explanation_teacher_paragraph_1\":\"Dyma’r Adnodd Trefnu. Gallwch ei ddefnyddio i greu grwpiau o apwyntiadau y gall eich myfyrwyr gofrestru ar eu cyfer.\",\"scheduler_explanation_teacher_paragraph_2\":\"Er enghraifft, efallai eich bod am drafod aseiniad y papur hanner tymor â holl fyfyrwyr eich cwrs. Byddech chi’n creu grŵp o apwyntiadau newydd, yn nodi’r dyddiadau a’r amseroedd pan rydych chi ar gael, yn dweud wrth y system am rannu’r amser hwnnw’n flociau o chwarter awr, ac yn caniatáu dim ond un myfyriwr ar gyfer pob bloc amser.\",\"scheduler_explanation_teacher_paragraph_3\":\"Yna byddai’r myfyrwyr yn cael gwybod am hynny ac yn gweld bod angen iddyn nhw gofrestru ar gyfer un o’r blociau amser penodol. Byddan nhw’n gweld calendr gyda’r blociau amser sydd ar gael yn ymddangos uwchben popeth arall ar eu calendrau eraill, er mwyn iddyn nhw allu dewis amser sy’n gyfleus iddyn nhw.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Gallwch ddechrau arni nawr drwy glicio’r botwm \\\"Creu grŵp apwyntiadau\\\" ar y dde. *\",\"signed_up_for\":\"*%{title} grŵp apwyntiadau* Rydych chi wedi cofrestru ar gyfer\",\"x_groups_have_signed_up\":\"%{n_participant_count} grŵp wedi cofrestru\",\"x_people_have_signed_up\":\"%{n_participant_count} person wedi cofrestru\"}},\"message_groups_who_1ba736f\":\"Anfon neges at grwpiau sydd...\",\"message_students_who_369b8488\":\"Anfon neges at fyfyrwyr sydd...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} - grŵp apwyntiadau* Gallwch **fewngofnodi** ar gyfer hyn.\"},\"da\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Slet\",\"edit\":\"Rediger\",\"location\":\"Sted:\",\"manage\":\"Administrering af %{title} aftalegruppe\",\"scheduler_explanation_student_header\":\"Du har ikke nogen aftalegrupper, du kan melde dig til\",\"scheduler_explanation_student_paragraph_1\":\"Lærere kan bruge dette værktøj til at planlægge ting, som de studerende kan tilmelde sig, f.eks kontortid, gruppepræsentationer, mv.\",\"scheduler_explanation_student_paragraph_2\":\"I øjeblikket har ingen af ​​dine lærere gjort nogen aftaler til rådighed. Du får besked, når aftaletidsrum bliver tilgængelige.\",\"scheduler_explanation_teacher_header\":\"Du har ikke oprettet nogen aftalegruppe, og du kan ikke tilmelde dig nogen\",\"scheduler_explanation_teacher_paragraph_1\":\"Dette er planlægningsværktøjet. Du kan bruge det til at oprette aftalegrupper, som de studerende kan melde sig til.\",\"scheduler_explanation_teacher_paragraph_2\":\"Lad os for eksempel forestille os, at du vil gennemgå den skriftlige terminsprøve med hver enkel studerende i dit fag. Du ville så oprette en ny aftalegruppe, angive datoerne og tidspunkterne, som du er til rådighed. Få værktøjet til at opdele den pågældende tid i blokke på 15 minutter og kun tillade en studerende pr. tidsblok.\",\"scheduler_explanation_teacher_paragraph_3\":\"De studerende får besked og kan se, at de skal melde sig til en af de specificerede tidsblokke. De vises en kalender med de tilgængelige tidsblokke oven på alt andet i deres andre kalendere, så de kan vælge et tidspunkt, som passer dem.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kom i gang nu ved at klikke på knappen \\\"Opret en aftalegruppe\\\" i højre side.*\",\"signed_up_for\":\"*%{title} aftalegruppe* Du er meldt til\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupper har tilmeldt sig\",\"x_people_have_signed_up\":\"%{n_participant_count} deltagere har tilmeldt sig\"}},\"message_groups_who_1ba736f\":\"Send besked til grupper, der ...\",\"message_students_who_369b8488\":\"Send besked til studerende, der ...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} aftalegruppe* Du kan **melde dig** til denne.\"},\"da-x-k12\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Slet\",\"edit\":\"Rediger\",\"location\":\"Sted:\",\"manage\":\"Administrering af %{title} aftalegruppe\",\"scheduler_explanation_student_header\":\"Du har ikke nogen aftalegrupper, du kan melde dig til\",\"scheduler_explanation_student_paragraph_1\":\"Lærere kan bruge dette værktøj til at planlægge ting, som eleverne kan tilmelde sig, fx kontortid, gruppepræsentationer, mv.\",\"scheduler_explanation_student_paragraph_2\":\"I øjeblikket har ingen af ​​dine lærere gjort nogen aftaler til rådighed. Du får besked, når aftaletidsrum bliver tilgængelige.\",\"scheduler_explanation_teacher_header\":\"Du har ikke oprettet nogen aftalegruppe, og du kan ikke tilmelde dig nogen\",\"scheduler_explanation_teacher_paragraph_1\":\"Dette er planlægningsværktøjet. Du kan bruge det til at oprette aftalegrupper, som eleverne kan melde sig til.\",\"scheduler_explanation_teacher_paragraph_2\":\"Lad os for eksempel forestille os, at du vil gennemgå den skriftlige terminsprøve med hver enkel elev i dit fag. Du ville så oprette en ny aftalegruppe, angive datoerne og tidspunkterne, som du er til rådighed. Få værktøjet til at opdele den pågældende tid i blokke på 15 minutter og kun tillade en elev pr. tidsblok.\",\"scheduler_explanation_teacher_paragraph_3\":\"Eleverne får besked og kan se, at de skal melde sig til en af de specificerede tidsblokke. De vises en kalender med de tilgængelige tidsblokke oven på alt andet i deres andre kalendere, så de kan vælge et tidspunkt, som passer dem.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kom i gang nu ved at klikke på knappen \\\"Opret en aftalegruppe\\\" i højre side.*\",\"signed_up_for\":\"*%{title} aftalegruppe* Du er meldt til\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupper har tilmeldt sig\",\"x_people_have_signed_up\":\"%{n_participant_count} deltagere har tilmeldt sig\"}},\"message_groups_who_1ba736f\":\"Send besked til grupper, der ...\",\"message_students_who_369b8488\":\"Send besked til elever, der ...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} aftalegruppe* Du kan **melde dig** til denne.\"},\"de\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Löschen\",\"edit\":\"Ändern\",\"location\":\"Standort:\",\"manage\":\"%{title}-Termingruppe verwalten\",\"scheduler_explanation_student_header\":\"Sie haben keine Termingruppen, für die Sie sich anmelden müssen.\",\"scheduler_explanation_student_paragraph_1\":\"Lehrer können mit diesem Tool Termine planen, für die sich ihre Studenten anmelden müssen, z. B. „Sprechstunden“, „Zeitfenster für Gruppenpräsentationen“ usw.\",\"scheduler_explanation_student_paragraph_2\":\"Bisher hat keiner Ihrer Lehrer Termine eingestellt. Sie werden benachrichtigt, wenn Termine zur Verfügung stehen.\",\"scheduler_explanation_teacher_header\":\"Sie haben keine Termingruppen erstellt und es gibt keine, für die Sie sich anmelden können.\",\"scheduler_explanation_teacher_paragraph_1\":\"Dies ist das Terminplaner-Tool. Sie können es nutzen, um Termingruppen zu erstellen, für die sich Ihre Studenten anmelden können.\",\"scheduler_explanation_teacher_paragraph_2\":\"Nehmen wir an, Sie wollen mit jedem der Studenten Ihres Kurses die schriftliche Arbeit zur Semestermitte durchgehen. Sie erstellen also eine neue Termingruppe, geben die Tage und Uhrzeiten an, zu denen Sie verfügbar sind, lassen sie diese Zeiten in 15-minütige Blöcke aufteilen, wobei pro Block ein Student zugelassen wird.\",\"scheduler_explanation_teacher_paragraph_3\":\"Anschließend erhalten die Studenten eine Benachrichtigung, dass sie sich für einen der Zeitblöcke anmelden sollen. Ein Kalender mit den verfügbaren Zeitblöcken wird ihnen über ihre restlichen Kalender gelegt, so dass sie eine für sie günstige Zeit auswählen können.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Beginnen Sie jetzt damit, rechts auf die Schaltfläche „Eine Termingruppe erstellen“ zu klicken. *\",\"signed_up_for\":\"Die *%{title} Termingruppe*, für die Sie angemeldet sind\",\"x_groups_have_signed_up\":\"%{n_participant_count} Gruppen haben sich angemeldet\",\"x_people_have_signed_up\":\"%{n_participant_count} Personen haben sich angemeldet\"}},\"message_groups_who_1ba736f\":\"Gruppen benachrichtigen, die...\",\"message_students_who_369b8488\":\"Studenten benachrichtigen, die ...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} Termingruppe* Sie können sich hierfür **anmelden**.\"},\"el\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Διαγραφή\",\"edit\":\"Διαμόρφωση\",\"location\":\"Τοποθεσία:\",\"scheduler_explanation_student_header\":\"Δεν υπάρχουν ομάδες συνάντησης για να εγγραφείτε\",\"scheduler_explanation_teacher_header\":\"Δεν έχετε δημιουργήσει καμία ομάδα συνάντησης και δεν υπάρχει καμία διαθέσιμη για εγγραφή\",\"scheduler_explanation_teacher_paragraph_1\":\"Αυτός είναι ο Μηχανισμός Προγραμματισμού Συναντήσεων. Μπορείτε να τον χρησιμοποιήσετε για να δημιουργήσετε ομάδες συναντήσεων για να εγγραφούν σε αυτές οι φοιτητές/τριές σας.\",\"scheduler_explanation_teacher_paragraph_2\":\"Για παράδειγμα, ας πούμε ότι θέλετε να συζητήσετε την εργασία του τριμήνου με καθέναν από τους φοιτητές στο μάθημά σας. Θα μπορούσατε να δημιουργήσετε ομάδα συνάντησης, να ορίσετε τις ημερομηνίες και τις ώρες κατά τις οποίες είστε διαθέσιμος/-η, να ζητήσετε να κατανεμηθεί ο χρόνος αυτός σε 15λεπτες περιόδους και να επιτρέπεται να εγγραφεί μόνο ένας σπουδαστής σε κάθε 15λεπτη περίοδο.\",\"scheduler_explanation_teacher_paragraph_3\":\"\\u003cmrk mid=\\\"278\\\" mtype=\\\"seg\\\"\\u003eΟι σπουδαστές θα λάμβαναν σχετική ειδοποίηση και θα έβλεπαν ότι πρέπει να εγγραφούν σε μία από τις προκαθορισμένες περιόδους.\\u003c/mrk\\u003e \\u003cmrk mid=\\\"279\\\" mtype=\\\"seg\\\"\\u003eΘα μπορούσαν να προβάλουν ένα ημερολόγιο με τις διαθέσιμες περιόδους που θα έπεφταν επάνω από όλες τις υπόλοιπες καταχωρίσεις στα ημερολόγιά τους ώστε να επιλέξουν την περίοδο που θα τους εξυπηρετούσε.\\u003c/mrk\\u003e\"}},\"message_groups_who_1ba736f\":\"Στείλτε μήνυμα στις ομάδες που...\",\"message_students_who_369b8488\":\"Στείλτε Μήνυμα στους Σπουδαστές Που...\"},\"en-AU\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Delete\",\"edit\":\"Edit\",\"location\":\"Location:\",\"manage\":\"Manage %{title} appointment group\",\"scheduler_explanation_student_header\":\"You do not have any appointment groups to sign up for\",\"scheduler_explanation_student_paragraph_1\":\"Teachers can use this tool to schedule things for their students to sign up for, e.g. office hours, group presentation time slots, etc.\",\"scheduler_explanation_student_paragraph_2\":\"Currently, none of your teachers have any appointments available. You will be notified if appointment slots become available.\",\"scheduler_explanation_teacher_header\":\"You have not created any appointment groups, and there are none you can sign up for\",\"scheduler_explanation_teacher_paragraph_1\":\"This is the Scheduler Tool. You can use it to create appointment groups for your students to sign up for.\",\"scheduler_explanation_teacher_paragraph_2\":\"For example, lets say you want to go over the midterm paper assignment with each of the students in your course. You would create a new appointment group, specify the dates and times that you're available, tell it to split that time into 15 minute blocks and only allow one student per time-block.\",\"scheduler_explanation_teacher_paragraph_3\":\"Then students would get notified and see that they need to sign up for one of the specified time blocks. They will be shown a calendar with the available time blocks overlaid on top of everything on their other calendars so they can pick a time that works for them.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Get started now by clicking the \\\"Create an appointment group\\\" button to the right. *\",\"signed_up_for\":\"*%{title} appointment group* you're signed up for\",\"x_groups_have_signed_up\":\"%{n_participant_count} groups have signed up\",\"x_people_have_signed_up\":\"%{n_participant_count} people have signed up\"}},\"message_groups_who_1ba736f\":\"Message Groups Who...\",\"message_students_who_369b8488\":\"Message Students Who...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} appointment group* You can **sign up** for this.\"},\"en-CA\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Delete\",\"edit\":\"Edit\",\"location\":\"Location:\",\"manage\":\"Manage %{title} appointment group\",\"scheduler_explanation_student_header\":\"You do not have any appointment groups to sign up for\",\"scheduler_explanation_student_paragraph_1\":\"Teachers can use this tool to schedule things for their students to sign up for, e.g. office hours, group presentation time slots, etc.\",\"scheduler_explanation_student_paragraph_2\":\"Currently, none of your teachers have made any appointments available. You will be notified if appointment slots become available.\",\"scheduler_explanation_teacher_header\":\"You have not created any appointment groups, and there are none you can sign up for\",\"scheduler_explanation_teacher_paragraph_1\":\"This is the Scheduler Tool. You can use it to create appointment groups for your students to sign up for.\",\"scheduler_explanation_teacher_paragraph_2\":\"For example, lets say you want to go over the midterm paper assignment with each of the students in your course. You would create a new appointment group, specify the dates and times that you're available, tell it to split that time into 15 minute blocks and only allow one student per time-block.\",\"scheduler_explanation_teacher_paragraph_3\":\"Then students would get notified and see that they need to sign up for one of the specified time blocks. They will be shown a calendar with the available time blocks overlaid on top of everything on their other calendars so they can pick a time that works for them.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Get started now by clicking the \\\"Create an appointment group\\\" button to the right. *\",\"signed_up_for\":\"*%{title} appointment group* You're signed up for\",\"x_groups_have_signed_up\":\"%{n_participant_count} groups have signed up\",\"x_people_have_signed_up\":\"%{n_participant_count} people have signed up\"}},\"message_groups_who_1ba736f\":\"Message Groups Who...\",\"message_students_who_369b8488\":\"Message Students Who...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} appointment group* You can **sign up** for this.\"},\"en-GB\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Delete\",\"edit\":\"Edit\",\"location\":\"Location:\",\"manage\":\"Manage %{title} appointment group\",\"scheduler_explanation_student_header\":\"You do not have any appointment groups to sign up for\",\"scheduler_explanation_student_paragraph_1\":\"Teachers can use this tool to schedule actions for their students to sign up for, e.g. office hours, group presentation time slots, etc.\",\"scheduler_explanation_student_paragraph_2\":\"Currently, none of your teachers have made any appointments available. You will be notified if appointment slots become available.\",\"scheduler_explanation_teacher_header\":\"You have not created any appointment groups, and there are none you can sign up for\",\"scheduler_explanation_teacher_paragraph_1\":\"This is the scheduler tool. You can use it to create appointment groups for your students to sign up for.\",\"scheduler_explanation_teacher_paragraph_2\":\"For example, lets say you want to go over the midterm paper assignment with each of the students in your course. You would create a new appointment group, specify the dates and times that you're available, tell it to split that time into 15 minute blocks and only allow one student per time-block.\",\"scheduler_explanation_teacher_paragraph_3\":\"Then students would get notified and see that they need to sign up for one of the specified time blocks. They will be shown a calendar with the available time blocks overlaid on top of everything on their other calendars so they can pick a time that works for them.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Get started now by clicking the \\\"Create an appointment group\\\" button to the right. *\",\"signed_up_for\":\"*%{title} appointment group* You're signed up for\",\"x_groups_have_signed_up\":\"%{n_participant_count} groups have signed up\",\"x_people_have_signed_up\":\"%{n_participant_count} people have signed up\"}},\"message_groups_who_1ba736f\":\"Message groups who...\",\"message_students_who_369b8488\":\"Message students who...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} appointment group* You can **sign up** for this.\"},\"es\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Eliminar\",\"edit\":\"Editar\",\"location\":\"Ubicación:\",\"manage\":\"Administrar grupo de citas de %{title}\",\"scheduler_explanation_student_header\":\"No tiene ningún grupo de citas en el que inscribirse\",\"scheduler_explanation_student_paragraph_1\":\"Los profesores pueden usar esta herramienta para programar actividades para que los estudiantes se inscriban, p ej., horarios, intervalos de tiempo para presentaciones de grupos, etc.\",\"scheduler_explanation_student_paragraph_2\":\"En este momento, ninguno de los docentes ha habilitado algún turno. Se dará aviso en caso de que haya turnos disponibles.\",\"scheduler_explanation_teacher_header\":\"No ha creado un grupo de citas, ni hay uno en el que se pueda inscribir\",\"scheduler_explanation_teacher_paragraph_1\":\"Esta es la Herramienta del Planificador La puede usar para crear grupos de citas para que sus estudiantes puedan inscribirse.\",\"scheduler_explanation_teacher_paragraph_2\":\"Por ejemplo, digamos que desea repasar una tarea de ensayo de mitad del período con cada uno de los estudiantes de su curso. Crearía un nuevo grupo de citas, especificaría las fechas y horarios que tiene disponibles, le diría que puede dividir ese tiempo en bloques de 15 minutos y asignar a cada estudiante un solo bloque.\",\"scheduler_explanation_teacher_paragraph_3\":\"Después, los estudiantes recibirían una notificación en la que verán que deben inscribirse en uno de los bloques. Se les mostrará un calendario con los bloques disponibles resaltados sobre el resto de su calendario para que puedan seleccionar la hora que más les convenga.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Empiece ahora mismo haciendo clic en el botón \\\\\\\"Crear una cita de grupo\\\\\\\" que aparece a la derecha. *\",\"signed_up_for\":\"*%{title} grupo de cita* Está inscrito para\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupos se han registrado\",\"x_people_have_signed_up\":\"%{n_participant_count} personas se han registrado\"}},\"message_groups_who_1ba736f\":\"Mensaje a grupos que...\",\"message_students_who_369b8488\":\"Enviar un mensaje a estudiantes que...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} grupo de cita* Puede **inscribirse** para este.\"},\"fa\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"حذف\",\"edit\":\"ویرایش\",\"location\":\"مکان:\",\"manage\":\"مدیریت گروه قرار ملاقات %{title}\",\"scheduler_explanation_student_header\":\"هیچ گونه گروه قرار ملاقات برای ملحق شدن به آن وجود ندارد\",\"scheduler_explanation_student_paragraph_1\":\"اساتید می‌توانند از این ابزار برای زمان بندی ثبت نام دانشجویان خود  استفاده کنند، برای مثال ساعات اداری، بازه زمانی ارائه گروهی و غیره\",\"scheduler_explanation_student_paragraph_2\":\"در حال حاضر هیچ کدام از اساتید شما هیچ قرار ملاقات در دسترس ندارند. اگر بازه زمانی ملاقات در دسترس قرار گیرد، مطلع خواهید شد.\",\"scheduler_explanation_teacher_header\":\"هیچ گروه قرار ملاقات ایجاد نکرده اید و هیچ موردی که بتوانید در آن ثبت نام کنید، وجود ندارد\",\"scheduler_explanation_teacher_paragraph_1\":\"این ابزار، زمان بند است. می توانید از آن برای ایجاد گروه های قرار ملاقات استفاده کنید تا دانشجویان شما در آنها ثبت نام کنند.\",\"scheduler_explanation_teacher_paragraph_2\":\"برای مثال، فرض کنید که می خواهید تکلیف مقاله میان ترم را با هر یک از دانشجویان درس خود مرور کنید. ابتدا یک گروه قرار ملاقات جدید ایجاد می کنید، تاریخ ها و زمان هایی که در دسترس هستید را ایجاد می کنید، تعیین می کنید که این زمان به بازه های 15 دقیقه ای تقسیم شود و در هر بازه فقط به یک دانشجو امکان  می دهید.\",\"scheduler_explanation_teacher_paragraph_3\":\"سپس دانشجویان مطلع می شوند و می فهمند که باید در یکی از بازه های زمانی تعیین شده ثبت نام کنند. یک تقویم به همراه بازه های زمانی موجود که در بالای هر چیزی در تقویم های دیگر آنها جایگذاری شده است به آنها نشان داده خواهد شد تا آنها بتوانند زمانی که برایشان مناسب است را انتخاب نمایند.\",\"scheduler_explanation_teacher_paragraph_4\":\"* اکنون با کلیک کردن روی دکمه «ایجاد یک گروه قرار ملاقات» در سمت راست، شروع به کار کنید. *\",\"signed_up_for\":\"*گروه قرار ملاقات %{title}* که ثبت نام کرده اید\",\"x_groups_have_signed_up\":\"%{n_participant_count} گروه ثبت نام کرده اند.\",\"x_people_have_signed_up\":\"%{n_participant_count} نفر ثبت نام کرده‌اند.\"}},\"message_groups_who_1ba736f\":\"ارسال پیام برای گروه هایی که...\",\"message_students_who_369b8488\":\"ارسال پیام برای دانشجویانی که...\"},\"fi\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Poista\",\"edit\":\"Muokkaa\",\"location\":\"Sijainti:\",\"manage\":\"Hallitse %{title} tapaamisryhmää\",\"scheduler_explanation_student_header\":\"Sinulla ei ole yhtään tapaamisryhmää, johon voisit kirjoittautua\",\"scheduler_explanation_student_paragraph_1\":\"Opettajat voivat aikatauluttaa tämän työkalun avulla asioita opiskelijoilleen kirjoittautumista varten esim. toimistotunneille, ryhmäesityksiin, jne.\",\"scheduler_explanation_student_paragraph_2\":\"Tällä hetkellä kukaan opettajistasi ei ole ilmoittanut yhtään tapaamista saatavissa olevaksi. Sinulle ilmoitetaan, jos tapaamisaikoja tulee saataville.\",\"scheduler_explanation_teacher_header\":\"Et ole luonut tapaamisryhmiä eikä ole yhtään, joihin voisit rekisteröityä\",\"scheduler_explanation_teacher_paragraph_1\":\"Tämä on aikataulutusväline. Voit käyttää sitä luodaksesi tapaamisryhmiä opiskelijoille, joihin he voivat rekisteröityä.\",\"scheduler_explanation_teacher_paragraph_2\":\"Sanotaan esimerkiksi, että haluat katsoa kaikki kauden puolivälin testin jokaisen opiskelijan kanssa kurssillasi. Loisit uuden tapaamisryhmän, määrittäisit päivämäärät ja ajat, jolloin olet saatavissa, käsket jakaa kyseisen ajan 15 minuutin lohkoihin ja sallit vain yhden opiskelijan aikalohkoa kohti.\",\"scheduler_explanation_teacher_paragraph_3\":\"Opiskelijat saavat sitten ilmoituksen ja näkevät, että heidän täytyy rekisteröityä yhteen määritettyyn aikalohkoon. He näkyvät kalenterissa saatavissa olevien aikalohkojen kanssa, joilla on päällekkäisiä ajanvarauksia kaiken kanssa heidän muissa kalentereissaan, niin että he voivat valita heille sopivan ajan.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Aloita nyt napsauttamalla \\\"Luo tapaamisryhmä\\\"  -painiketta oikealla. *\",\"signed_up_for\":\"*%{title} tapaamisryhmä* Olet rekisteröitynyt\",\"x_groups_have_signed_up\":\"%{n_participant_count} ryhmää on rekisteröitynyt\",\"x_people_have_signed_up\":\"%{n_participant_count} ihmistä on rekisteröitynyt\"}},\"message_groups_who_1ba736f\":\"Viesti ryhmille, jotka...\",\"message_students_who_369b8488\":\"Viesti opiskelijoille, jotka...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} ajanvarausryhmä* Voit **rekisteröityä** tähän.\"},\"fr\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Supprimer\",\"edit\":\"Modifier\",\"location\":\"Lieu :\",\"manage\":\"Gérer le groupe de rendez-vous %{title}\",\"scheduler_explanation_student_header\":\"Vous n’avez aucun groupe de rendez-vous auxquels vous pouvez vous inscrire.\",\"scheduler_explanation_student_paragraph_1\":\"Les enseignants peuvent utiliser cet outil pour planifier des choses auxquelles les élèves peuvent s'inscrire, par ex. entretiens individuels, créneaux d'exposés en groupe, etc.\",\"scheduler_explanation_student_paragraph_2\":\"Actuellement, aucun de vos enseignants n'a de créneau disponible pour un rendez-vous. Vous serez prévenu si des créneaux se libèrent.\",\"scheduler_explanation_teacher_header\":\"Vous n’avez créé aucun groupe de rendez-vous ; vous ne pouvez vous inscrire à aucun groupe.\",\"scheduler_explanation_teacher_paragraph_1\":\"Voici l’outil de planification. Vous pouvez l’utiliser pour créer des groupes de rendez-vous auxquels vos étudiants peuvent s’inscrire.\",\"scheduler_explanation_teacher_paragraph_2\":\"Par exemple, supposons que vous souhaitiez revoir le travail sur table de la mi-trimestre avec chaque étudiant du cours. Vous créez un nouveau groupe de rendez-vous, vous indiquez les dates et heures auxquelles vous êtes disponibles, vous demandez à l’outil de répartir ce temps en tranches horaires de 15 minutes et vous n’autorisez qu’un seul étudiant par tranche horaire.\",\"scheduler_explanation_teacher_paragraph_3\":\"Les étudiants seront avisés et verront qu’ils doivent s’inscrire à l’une des tranches horaires indiquées. Un calendrier avec les tranches horaires disponibles s’affichera au-dessus de toutes les activités de leurs autres calendriers pour qu’ils choisissent un horaire qui leur convient.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Commencez en cliquant sur le bouton « Créer un groupe de rendez-vous » sur la droite. *\",\"signed_up_for\":\"*Le groupe de rendez-vous %{title}* auquel vous êtes inscrit\",\"x_groups_have_signed_up\":\"%{n_participant_count} groupes se sont inscrits\",\"x_people_have_signed_up\":\"%{n_participant_count} personnes se sont inscrites\"}},\"message_groups_who_1ba736f\":\"Envoyer un message aux groupes qui...\",\"message_students_who_369b8488\":\"Envoyer un message aux étudiants qui...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*Le groupe de rendez-vous %{title}* Vous pouvez **vous inscrire** à ceci.\"},\"fr-CA\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Supprimer\",\"edit\":\"Modifier\",\"location\":\"Emplacement :\",\"manage\":\"Gérer le groupe de rendez-vous %{title} \",\"scheduler_explanation_student_header\":\"Vous n’avez aucun groupe de rendez-vous auxquels vous pouvez vous inscrire.\",\"scheduler_explanation_student_paragraph_1\":\"Les enseignants peuvent utiliser cet outil de planification pour programmer des événements auxquels leurs étudiants peuvent s’inscrire, comme « heures de bureau » , « créneaux horaires de présentation de groupe » ou « déjeuner avec un ancien étudiant ».\",\"scheduler_explanation_student_paragraph_2\":\"Aucun de vos enseignants n’a créé de rendez-vous auxquels vous pouvez vous inscrire. Si quelqu’un crée un événement, vous en serez informé et vous pourrez vous y inscrire.\",\"scheduler_explanation_teacher_header\":\"Vous n’avez créé aucun groupe de rendez-vous; vous ne pouvez vous inscrire à aucun groupe.\",\"scheduler_explanation_teacher_paragraph_1\":\"Voici l’outil de planification. Vous pouvez l’utiliser pour créer des groupes de rendez-vous auxquels vos étudiants peuvent s’inscrire.\",\"scheduler_explanation_teacher_paragraph_2\":\"Par exemple, supposons que vous souhaitiez revoir le tâche sur table de la mi-trimestre avec chaque étudiant du cours. Vous créez un nouveau groupe de rendez-vous, vous indiquez les dates et heures auxquelles vous êtes disponibles, vous demandez à l’outil de répartir ce temps en tranches horaires de 15 minutes et vous n’autorisez qu’un seul étudiant par tranche horaire.\",\"scheduler_explanation_teacher_paragraph_3\":\"Les étudiants seront avisés et verront qu’ils doivent s’inscrire à l’une des tranches horaires indiquées. Un calendrier avec les tranches horaires disponibles s’affichera au-dessus de toutes les activités de leurs autres calendriers pour qu’ils choisissent un horaire qui leur convient.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Commencez en cliquant sur le bouton « Créer un groupe de rendez-vous » sur la droite. *\",\"signed_up_for\":\"*Le groupe de rendez-vous *%{title} auquel vous êtes inscrit\",\"x_groups_have_signed_up\":\"%{n_participant_count} les groupes ont signé\",\"x_people_have_signed_up\":\"%{n_participant_count} les gens ont signé\"}},\"message_groups_who_1ba736f\":\"Envoyer un message aux groupes qui...\",\"message_students_who_369b8488\":\"Envoyer un message aux étudiants qui...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*Le groupe de rendez-vous *%{title} Vous pouvez **vous inscrire** à ceci.\"},\"he\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"ביטול\",\"edit\":\"עריכה\",\"location\":\"מיקום:\",\"manage\":\"ניהול פגישת קבוצה %{title} \",\"scheduler_explanation_student_header\":\"לא קיימות עבורך קבוצות מפגש כלשהן להרשמה\",\"scheduler_explanation_student_paragraph_1\":\"מורים יכולים להשתמש בכלי זה כדי לתזמן ארועים לתלמידים אליהם יוכלו להרשם, כלומר חלנות זמן לשעות משרד, זמני מצגת קבוצתית וכדומה.\",\"scheduler_explanation_student_paragraph_2\":\"בשלב זה אף אחד ממוריך לא פתח חלון זמין לפגישות. תקבל הודעה כאשר חלון כזה יפתח.\",\"scheduler_explanation_teacher_header\":\"לא יצרת כל קבוצת מפגש שהיא, ואין כאלו שניתן לך להרשם אליהן\",\"scheduler_explanation_teacher_paragraph_1\":\"זהו כלי תזמון. תוכל להשתמש בו כדי ליצור קבוצות מפגש אליהם תלמידיך יוכלו להרשם.\",\"scheduler_explanation_teacher_paragraph_2\":\"לדוגמה, נניח שתרצו לעיין בעבודת ביניים של המחצית יחד עם כל אחד מהתלמידים בקורס. ניתן ליצור קבוצת מפגש חדשה, להגדיר את המועדים והזמנים בהם הנכם זמינים, להגדיר חלוקת זמן לחלונות של 15 דקות ולאפשר לתלמיד אחד להרשם לחלון זמן ספציפי.\",\"scheduler_explanation_teacher_paragraph_3\":\"הודעות תשלחנה לתלמידים כדי שידעו שעליהם להרשם לאחד מחלונות הזמן. אלו יוצגו ביומן בצד כל האירועים האחרים ביומן כדי להקל על התלמידים לבחור חלון זמן מתאים.\",\"scheduler_explanation_teacher_paragraph_4\":\"* התחל/י פעילות כעת ע\\\"י הקלקה על כפתור \\\"יצירת קבוצת מפגש\\\" שבצד המסך.\\\"\",\"signed_up_for\":\"*פגישת קבוצה %{title}* אליה נרשמת\",\"x_groups_have_signed_up\":\"%{n_participant_count} קבוצות משתתפים נרשמו\",\"x_people_have_signed_up\":\"%{n_participant_count} אנשים נרשמו\"}},\"message_groups_who_1ba736f\":\"קבוצות הודעות אשר...\",\"message_students_who_369b8488\":\"הודעות לתלמידים אשר...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*קבוצת פגישה %{title} * באפשרותך  **להרשם ** לזה.\"},\"ht\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Efase\",\"edit\":\"Modifye\",\"location\":\"Anplasman:\",\"manage\":\"Jere gwoup randevou %{title}\",\"scheduler_explanation_student_header\":\"Ou pa genyen gwoup randevou pou enskri\",\"scheduler_explanation_student_paragraph_1\":\"Pwofesè yo kapab itilize enstriman sa a pou yo ka pwograme bagay pou elèv yo ka enskri ladan yo, tankou lè biwo, entèval tan prezantasyon gwoup elatriye.\",\"scheduler_explanation_student_paragraph_2\":\"Pou kounye a, pa gen okenn nan pwofesè ou yo ki mete randevou disponib. Y ap avèti w si gen entèval tan randevou ki vin disponib.\",\"scheduler_explanation_teacher_header\":\"Ou pa kreye okenn gwoup randevou, ni pa genyen ou kapab enskri ladan yo.\",\"scheduler_explanation_teacher_paragraph_1\":\"Sa se enstriman Planifikatè a. Ou pakab itilize li pou kreye gwoup randevou pou elèv ou yo ka enskri.\",\"scheduler_explanation_teacher_paragraph_2\":\"Pa egzanp, an nou di ou vle pase sou sesyon papye a mi pakou ak chak grenn nan elèv ki nan kou ou a. Ou ta dwe kreye yon nouvo group randevou, presize nan ki dat e a ki lè w ap disponib, di pou divize tan sa a an blòk de 15 minit epi otorize sèlman yon elèv pou chak blòk.\",\"scheduler_explanation_teacher_paragraph_3\":\"Y ap avèti elèv yo epi y ap wè yo dwe enskri nan yonn nan blòk tan yo presize yo. Gen yon kalandriye ki gen blòk tan ki disponib yo k ap afiche an sipèpozisyon anlè tout lòt kalandriye yo pou yo kapab chwazi yon lè k ap bon pou yo.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Klike sou touch \\\"Kreye yon gwoup randevou\\\" ki adwat la pou kòmanse kounye a menm. *\",\"signed_up_for\":\"*%{title} gwoup randevou* Ou enskri pou\",\"x_groups_have_signed_up\":\"%{n_participant_count} gwoup yo enskri\",\"x_people_have_signed_up\":\"%{n_participant_count} Moun yo enskri\"}},\"message_groups_who_1ba736f\":\"Voye Mesaj nan Gwoup Ki...\",\"message_students_who_369b8488\":\"Voye Mesaj bay Elèv Ki...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} gwoup randevou* Ou ka **enskri** nan sa a.\"},\"hu\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Törlés\",\"edit\":\"Szerkesztés\",\"location\":\"Helyszín:\",\"manage\":\"%{title} megbeszélés-csoport kezelése\",\"scheduler_explanation_student_header\":\"Önnek nincsen egy megbeszélés-csoportja sem, ahova feliratkozhatna\",\"scheduler_explanation_student_paragraph_1\":\"Az oktatók ezt az ütemezőt használhatják a hallgatók bejelentkeztetésére különböző találkozókra, időpontokra, például fogadóórára, konzultációs időpontra vagy csoportmunka bemutatására.\",\"scheduler_explanation_teacher_header\":\"Nem hozott létre megbeszélés-csoportot és nincs egy sem, amire fel tudna jelentkezni\",\"scheduler_explanation_teacher_paragraph_1\":\"Ez az Ütemező eszköz. Használhatja a megbeszélés-csoportok szervezésére, amelyekre a hallgatók feljelentkezhetnek.\",\"scheduler_explanation_teacher_paragraph_2\":\"Például, tegyük fel, hogy át szeretné beszélni a zárthelyiket a kurzus minden egyes hallgatójával. Ehhez hozzon létre egy új megbeszélés-csoportot, és adja meg a dátumokat és időpontokat, amikor Ön elérhető. Megteheti, hogy az időt 15 perces blokkokra osztja fel és egy időblokkba csak egy hallgatót enged feliratkozni.\",\"scheduler_explanation_teacher_paragraph_3\":\"Ezt követően a hallgatók értesítést kapnak, és látják, hogy fel kell jelentkezniük egy időblokkra a megadottak közül. Egy naptárat fognak látni az elérhető időblokkokkal és az egyéb naptárjaikkal összefűzve, így ki tudnak választani egy olyan időpontot ami nekik megfelelő.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kezdje el most úgy, hogy rányom a jobbra lévő \\\"Megbeszélés-csoport létrehozása\\\" gombra. *\",\"signed_up_for\":\"*%{title} megbeszélés-csoport* Ön fel van jelentkezve\",\"x_groups_have_signed_up\":\"%{n_participant_count} csoport jelentkezett fel\",\"x_people_have_signed_up\":\"%{n_participant_count} résztvevő jelentkezett fel\"}},\"message_groups_who_1ba736f\":\"Üzenet azon csoportoknak, akik...\",\"message_students_who_369b8488\":\"Üzenet azon hallgatóknak, akik...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} megbeszélés-csoport* Ön **feljelentkezhet**\"},\"hy\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Ջնջել\",\"edit\":\"Խմբագրել\",\"location\":\"Գտնվելու վայրը\",\"manage\":\"Կառավարել\",\"scheduler_explanation_student_header\":\"Դուք չունեք հանդիպումների խմբեր, որոնց անհրաժեշտ է գրանցվել\",\"scheduler_explanation_student_paragraph_1\":\"Դասավանդողները կարող են օգտագործել պլանավորման այս  գործիքը իրենց ունկնդիրների համար ծրագրելու միջոցառումներ, որոնց պետք է նրանք գրանցվեն: Օրինակ՝ ընդունելության ժամեր, խմբային շնորհանդեսների ժամանակահատված եւ այլն:\",\"scheduler_explanation_student_paragraph_2\":\"Ձեր դասավանդողներից ոչ մեկը չի նշանակել որևէ հանդիպում, որին դուք կարող էիք գրանցվել, ուստի այժմ դուք ոչինչ չունեք անելու: Ձեզ կծանուցվի, եթե դասավանդողներից որևէ մեկը ստեղծի ինչ-որ բան, որին դուք կարող եք գրանցվել: \",\"scheduler_explanation_teacher_header\":\"Դուք չեք ստեղծել հանդիպումների խմբեր, և գրանցման ենթակա հանդիպումներ չկան:\",\"scheduler_explanation_teacher_paragraph_1\":\"Սա պլանավորման գործիք է: Դուք կարող եք այն օգտագործել ձեր ունկնդիրների համար հանդիպումների խմբեր ստեղծելու համար, որոնց նրանք կարող են գրանցվել:\",\"scheduler_explanation_teacher_paragraph_2\":\"Ենթադրենք, դուք ցանկանում եք վերանայել Ձեր դասընթացի յուրաքանչյուր ունկնդրի միջանկյալ աշխատանքը: Դուք պետք է ստեղծեք նոր հանդիպումների խումբ, որոշեք Ձեզ հարմար ամսաթվերը և ժամերը, բաժանեք այդ ժամանակը 15-րոպեանոց միջակայքերի և  ընդունել միայն մեկ ունկնդրի այդ ժամանակային միջակայքում: \",\"scheduler_explanation_teacher_paragraph_3\":\"Ունկնդիրները կծանուցվեն այն մասին, որ պետք է գրանցվեն որևէ ժամանակային միջակայքում: Նրանք տեղեկացվելու են  ազատ միջակայքերի մասին, և համադրելով իրենց մյուս օրացուցային միջոցառումների հետ, նրանք կարող են ընտրել իրենց համար հարմար ժամանակը: \",\"signed_up_for\":\"Դուք գրանցվել եք\",\"x_groups_have_signed_up\":\"%{participant_count} խմբերում գրանցվել են\",\"x_people_have_signed_up\":\"%{participant_count} մարդ գրանցվել է\"}},\"message_groups_who_1ba736f\":\"Հաղորդագրությունների խմբեր, ովքեր...\",\"message_students_who_369b8488\":\"Հաղորդագրություններ ուղարկել ունկնդիրներին, ովքեր...\"},\"is\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Eyða\",\"edit\":\"Breyta\",\"location\":\"Staðsetning:\",\"manage\":\"Umsjón með fundahópi: %{title}\",\"scheduler_explanation_student_header\":\"Þú hefur enga fundahópa til að skrá þig í\",\"scheduler_explanation_student_paragraph_1\":\"Kennarar geta notað þetta tæki til að skipuleggja hluti sem nemendur geta skráð sig í, t.d. viðverutíma, tímaraufar fyrir hópkynningu, o.fl.\",\"scheduler_explanation_student_paragraph_2\":\"Engir kennara þinna hafa lausa fundartíma. Þú færð tilkynningu ef fundartímar losna.\",\"scheduler_explanation_teacher_header\":\"Þú hefur enga fundahópa stofnað og getur ekki skráð þig í neinn\",\"scheduler_explanation_teacher_paragraph_1\":\"Þetta er Skipuleggjaratól. Þú getur notað það til að stofna fundahópa sem nemendur geta skráð sig í.\",\"scheduler_explanation_teacher_paragraph_2\":\"Dæmi: Segjum að þú viljir fara yfir miðannarritgerðarverkefni með öllum nemendum í námskeiðinu þínu. Þú stofnar nýjan fundahóp, tiltekur daga og tíma sem þú ert laus, segir hópnum að skipta tímanum í 15 mínútna skeið og heimila bara einn nemanda á hvert skeið.\",\"scheduler_explanation_teacher_paragraph_3\":\"Þá fá nemendur tilkynningu og sjá að þeir þurfa að skrá sig fyrir tilteknum tíma. Þeir sjá dagatal með tiltækum tímabilum sem skarast yfir allt á öðrum dagatölum þeirra svo þeir geti valið tíma sem hentar þeim.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Byrjaðu núna með því að smella á hnappinn \\\"Stofna fundahóp\\\" hægra megin. *\",\"signed_up_for\":\"*%{title} fundahópur* Þú skráðir þig í\",\"x_groups_have_signed_up\":\"%{n_participant_count} hópar hafa skráð sig\",\"x_people_have_signed_up\":\"%{n_participant_count} einstaklingar hafa skráð sig\"}},\"message_groups_who_1ba736f\":\"Senda skilaboð til hópa sem...\",\"message_students_who_369b8488\":\"Senda skilaboð til nemenda sem...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} fundahópur* Þú getur  **skráð** þig í þetta.\"},\"it\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Elimina\",\"edit\":\"Modifica\",\"location\":\"Posizione:\",\"manage\":\"Gestisci gruppo appuntamenti %{title}\",\"scheduler_explanation_student_header\":\"Non hai nessun gruppo di appuntamenti a cui registrarti\",\"scheduler_explanation_student_paragraph_1\":\"Gli insegnanti possono utilizzare questo strumento per programmare le attività a cui devono registrarsi i loro studenti, ad es. orari d'ufficio, fasce orarie di presentazione dei gruppi e così via.\",\"scheduler_explanation_student_paragraph_2\":\"Attualmente, nessuno dei tuoi insegnanti ha impostato eventuali appuntamenti come disponibili. Ti verrà inviata una notifica se gli spazi per gli appuntamenti diventano disponibili.\",\"scheduler_explanation_teacher_header\":\"Non hai creato nessun gruppo di appuntamenti e non ci sono gruppi a cui puoi registrarti\",\"scheduler_explanation_teacher_paragraph_1\":\"Si tratta di un'utilità di pianificazione. Puoi utilizzarla per creare gruppi di appuntamenti a cui i tuoi studenti devono registrarsi.\",\"scheduler_explanation_teacher_paragraph_2\":\"Ad esempio, nel caso tu voglia riesaminare l'elaborato del compito di metà trimestre con ciascuno degli studenti nel tuo corso. Puoi creare un nuovo gruppo di appuntamenti, specificare le date e gli orari in cui sei disponibile, dividere il periodo di tempo in intervalli di 15 minuti e consentire un solo studente per ciascun intervallo di tempo.\",\"scheduler_explanation_teacher_paragraph_3\":\"Gli studenti riceveranno quindi una notifica in cui vengono informati che devono registrarsi a uno degli intervalli di tempo specificati. Agli studenti verrà mostrato un calendario con gli intervalli di tempo disponibili sovrapposti a tutti i periodi di tempo presenti negli altri loro calendari, così possono scegliere il periodo di tempo più adatto alle loro esigenze.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Inizia ora facendo clic sul pulsante \\\"Crea un gruppo di appuntamenti\\\" a destra. *\",\"signed_up_for\":\"*%{title} gruppo di appuntamenti* Ti sei registrato a\",\"x_groups_have_signed_up\":\"%{n_participant_count} gruppi registrati\",\"x_people_have_signed_up\":\"%{n_participant_count} persone registrate\"}},\"message_groups_who_1ba736f\":\"Invia messaggio ai gruppi che...\",\"message_students_who_369b8488\":\"Invia messaggio agli studenti che...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} gruppo di appuntamenti* Puoi **registrarti** per questo.\"},\"ja\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"削除\",\"edit\":\"編集\",\"location\":\"場所:\",\"manage\":\"%{title} 予定グループを管理する\",\"scheduler_explanation_student_header\":\"サインアップできる予定グループはありません\",\"scheduler_explanation_student_paragraph_1\":\"講師はこのツールを使用して、受講生がサインアップするスケジュールを立てることができます。つまり、勤務時間、グループプレゼンテーションのタイムスロットなどです。\",\"scheduler_explanation_student_paragraph_2\":\"現在、あなたの講師の誰もスケジュールを用意していません。予約スロットが利用可能になると通知されます。\",\"scheduler_explanation_teacher_header\":\"予定グループが作成されていないので、サインアップできる予定はありません\",\"scheduler_explanation_teacher_paragraph_1\":\"これはスケジューラ ツールです。講師はこのツールを使用して、受講生がサインアップする予定グループを作成できます。\",\"scheduler_explanation_teacher_paragraph_2\":\"例えば、講師がコースの各受講生に中間筆記課題を出すものとします。そのような場合は、新しい予定グループを作成し、自分の都合の良い日時を指定し、その時間を 15 分のブロックに分割して、各時間ブロックで 1 人の受講生だけを受け付けるようにします。\",\"scheduler_explanation_teacher_paragraph_3\":\"その後、受講生向けに、指定の時間ブロックのいずれかにサインアップする必要があることを通知します。受講生のカレンダーには、利用できる時間ブロックが他のすべての予定と共に表示されるので、受講生は都合のよい時間を選択できます。\",\"scheduler_explanation_teacher_paragraph_4\":\"* 今すぐ始めるには、右側の [予定グループを作成する] ボタンをクリックします。 *\",\"signed_up_for\":\"サインアップされる*%{title} 予定グループ*\",\"x_groups_have_signed_up\":\"%{n_participant_count} グループがサインアップしました\",\"x_people_have_signed_up\":\"%{n_participant_count} 人々がサインアップしました\"}},\"message_groups_who_1ba736f\":\"次の状態のグループにメッセージを送る...\",\"message_students_who_369b8488\":\"次の状態の受講者にメッセージを送る...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"これに**サインアップ** することができる*%{title} 予定グループ* 。\"},\"ko\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"삭제\",\"edit\":\"편집\",\"location\":\"위치:\",\"manage\":\"관리\",\"scheduler_explanation_student_header\":\"등록할 약속 그룹이 없음\",\"scheduler_explanation_student_paragraph_1\":\"교사는 이 일정 관리 도구를 이용하여 학생이 등록할 수 있는 일정을 배치할 수 있습니다. 예를 들어, \\\"근무 시간\\\", \\\"그룹 프레젠테이션 시간\\\", \\\"졸업생과 점심\\\" 등이 있습니다.\",\"scheduler_explanation_student_paragraph_2\":\"어떤 교사도 여러분이 등록할 수 있는 약속을 만들지 않아서 지금 할 일이 없습니다. 누군가 무엇을 만들면 여러분이 와서 등록할 수 있도록 알림을 보낼 것입니다.\",\"scheduler_explanation_teacher_header\":\"약속 그룹을 만들지 않아서 등록할 대상이 없음\",\"scheduler_explanation_teacher_paragraph_1\":\"이것은 일정 관리 도구입니다. 이는 학생이 등록할 수 있는 약속 그룹을 만드는 데 사용할 수 있습니다.\",\"scheduler_explanation_teacher_paragraph_2\":\"예를 들어, 과목의 각 학생과 중간 학기 보고서 과제를 이야기한다고 해봅시다. 여러분은 새 약속 그룹을 만들고, 사용 가능한 날짜와 시간을 지정한 후, 해당 시간을 15분 블록으로 나누고, 시간 블록 당 한 명의 학생만 허용하도록 할 수 있습니다.\",\"scheduler_explanation_teacher_paragraph_3\":\"그러면 학생은 지정된 시간 블록 중 하나에 등록하라는 알림을 받게 됩니다. 표시되는 달력은 다른 항목 위에 사용 가능한 시간이 겹쳐서 나오므로 가능한 시간을 고를 수 있습니다.\",\"x_groups_have_signed_up\":\"%{participant_count} 그룹이 등록함\",\"x_people_have_signed_up\":\"%{participant_count} 사용자가 등록함\"}},\"message_groups_who_1ba736f\":\"다음 그룹에게 메시지 보내기\",\"message_students_who_369b8488\":\"다음 학생에게 메시지 보내기\"},\"mi\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Muku\",\"edit\":\"Whakatika\",\"location\":\"Wāhi:\",\"manage\":\"Whakahaere %{title} rōpū whakarite\",\"scheduler_explanation_student_header\":\"E kore koe e whai i tētahi rōpū whakarite ki te haina ki runga hoki\",\"scheduler_explanation_student_paragraph_1\":\"Ka taea e ngā kaiako te whakamahi i tēnei taputapu ki te whakatakoto mea mō a rātou ākonga ki te haina, e.g. ngā haora o te tari, ngā wā whakāturanga kāniwha mō ngā rōpu etc\",\"scheduler_explanation_student_paragraph_2\":\"I tēnei wā kaore ō kaiako i whakatakoto he whakarite e wātea ana Ka whakamōhiotia koe mehemea ka wātea he whakarite kāniwha\",\"scheduler_explanation_teacher_header\":\"Kaore koe i hanga i tētahi rōpū whakarite, a ki reira tētahi taea e koe te haina ki runga hoki he\",\"scheduler_explanation_teacher_paragraph_1\":\"Ko te Taputapu Pūhōtaka tēnei. Ka taea e koe te whakamahi i te reira ki te hanga i ngā rōpū whakarite mo ki o koutou ākonga te haina ki runga hoki.\",\"scheduler_explanation_teacher_paragraph_2\":\"Hei tauira, ka taea e mea e hiahia ana koe ki te haere i runga i te whakataunga pepa waenganui i te wā ki ia o te ākonga i roto i to koutou akoranga. E waihanga koe i tētahi rōpū whakarite hou, whakapūtā te rā me ngā wā e wātea ana kei a koutou, kōrero ki te reira ki te wāhia e taua wa ki 15 poraka meneti, me te tukua tētahi ākonga ia te wā-poraka anake.\",\"scheduler_explanation_teacher_paragraph_3\":\"Na e kia whakamōhiotia, ka kite e hiahia ana ratou ki te haina ki runga mo tētahi o nga poraka wā i tohua ngā ākonga. Ka whakaaturia e ratou he maramataka ki te poraka wā e wātea ana i whakakikoruatia i runga i runga o te mea katoa i runga i te tahi atu ratou maramataka kia taea e ratou te tiki i te wa e mahi mo ratou.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kia tīmata ināianei te pāwhiri i te pātene \\\"Waihanga i tētahi rōpū whakarite\\\" ki te tika. *\",\"signed_up_for\":\"*%{title} rōpū whakarite* e hainatia e koe ki runga hoki\",\"x_groups_have_signed_up\":\"%{n_participant_count} Kua hainatia ngā rōpū\",\"x_people_have_signed_up\":\"%{n_participant_count} Kua hainatia ngā tāngata\"}},\"message_groups_who_1ba736f\":\"Rōpū Karere wai ...\",\"message_students_who_369b8488\":\"Karere ākonga wai ...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} rōpū whakarite* Ka taea e koe **haina ake** mo tēnei\"},\"nb\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Slett\",\"edit\":\"Rediger\",\"location\":\"Sted:\",\"manage\":\"Administrere %{title} avtale gruppe\",\"scheduler_explanation_student_header\":\"Du har ingen avtalegrupper å melde deg på\",\"scheduler_explanation_student_paragraph_1\":\"Lærere kan bruke dette verktøyet for å sette opp ting for studentene å melde seg på, for eksempel kontortid, tider for gruppepresentasjoner, osv.\",\"scheduler_explanation_student_paragraph_2\":\"Foreløpig har ingen av lærerne gjort noen avtaler tilgjengelig. Du vil bli varslet om avtaletidspunkter blir tilgjengelige.\",\"scheduler_explanation_teacher_header\":\"Du har ikke opprette noen avtalegruppe, og det er ingen du kan melde deg på\",\"scheduler_explanation_teacher_paragraph_1\":\"Dette er planleggingsverktøyet. Du kan benytte det for å legge til aktivitetsgrupper studentene kan melde seg på.\",\"scheduler_explanation_teacher_paragraph_2\":\"Et eksempel kan være at du vil gå gjennom en oppgaveinnlevering med hver av studentene i emnet ditt. Du lager en ny møtetid, spesifiserer datoer og tidspunkt du er tilgjengelig for deretter å be systemet å dele tidsrommet i 15-minutters blokker og kun tillate en student pr \\\"tidsblokk\\\".\",\"scheduler_explanation_teacher_paragraph_3\":\"Studentene vil så få beskjed og se at de må melde seg på en av de spesifiserte tidspunktene. De vil se en kalender med tilgjengelige tidspunkt som et \\\"overlegg\\\" på alt annet som ligger i deres kalender slik at de kan velge et tidspunkt som passer den enkelte.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kom i gang nå ved å klikke på \\\"Opprett en avtale gruppe\\\" knappen til høyre. *\",\"signed_up_for\":\"*%{title} avtalegruppe* der du er registrert\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupper har registrert seg\",\"x_people_have_signed_up\":\"%{n_participant_count} mennesker har registrert seg\"}},\"message_groups_who_1ba736f\":\"Send melding til grupper som...\",\"message_students_who_369b8488\":\"Send melding til studenter som...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} møtetid* Du kan **registrere deg** for dette.\"},\"nb-x-k12\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Slett\",\"edit\":\"Rediger\",\"location\":\"Sted:\",\"manage\":\"Administrere %{title} avtale gruppe\",\"scheduler_explanation_student_header\":\"Du har ingen avtalegrupper å melde deg på\",\"scheduler_explanation_student_paragraph_1\":\"Lærere kan bruke dette verktøyet for å sette opp ting for elevene å melde seg på, for eksempel kontortid, tider for gruppepresentasjoner, osv.\",\"scheduler_explanation_student_paragraph_2\":\"Foreløpig har ingen av lærerne gjort noen avtaler tilgjengelig. Du vil bli varslet om avtaletidspunkter blir tilgjengelige.\",\"scheduler_explanation_teacher_header\":\"Du har ikke opprette noen avtalegruppe, og det er ingen du kan melde deg på\",\"scheduler_explanation_teacher_paragraph_1\":\"Dette er planleggingsverktøyet. Du kan benytte det for å legge til aktivitetsgrupper elevene kan melde seg på.\",\"scheduler_explanation_teacher_paragraph_2\":\"Et eksempel kan være at du vil gå gjennom en oppgaveinnlevering med hver av elevene i faget ditt. Du lager en ny møtetid, spesifiserer datoer og tidspunkt du er tilgjengelig for deretter å be systemet å dele tidsrommet i 15-minutters blokker og kun tillate én elev pr. «tidsblokk».\",\"scheduler_explanation_teacher_paragraph_3\":\"Elevene vil så få beskjed og se at de må melde seg på en av de spesifiserte tidspunktene. De vil se en kalender med tilgjengelige tidspunkt som et \\\"overlegg\\\" på alt annet som ligger i deres kalender slik at de kan velge et tidspunkt som passer den enkelte.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kom i gang nå ved å klikke på \\\"Opprett en avtale gruppe\\\" knappen til høyre. *\",\"signed_up_for\":\"*%{title} avtalegruppe* der du er registrert\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupper har registrert seg\",\"x_people_have_signed_up\":\"%{n_participant_count} deltakere har registrert seg\"}},\"message_groups_who_1ba736f\":\"Send melding til grupper som...\",\"message_students_who_369b8488\":\"Send melding til elever som...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} møtetid* Du kan **registrere deg** for dette.\"},\"nl\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Verwijderen\",\"edit\":\"Bewerken\",\"location\":\"Locatie:\",\"manage\":\"%{title} afspraakgroep beheren\",\"scheduler_explanation_student_header\":\"Er zijn nog geen afspraakgroepen waarvoor je kunt inschrijven\",\"scheduler_explanation_student_paragraph_1\":\"Docenten kunnen deze tool gebruiken voor het inplannen van items zodat hun cursisten zich bijvoorbeeld kunnen aanmelden voor kantooruren, tijdblokken voor groepspresentaties, enzovoort.\",\"scheduler_explanation_student_paragraph_2\":\"Op dit moment heeft geen van je docenten tijdblokken voor afspraken beschikbaar gesteld. Je krijgt een melding als er tijdblokken voor afspraken beschikbaar komen.\",\"scheduler_explanation_teacher_header\":\"Je hebt nog geen afspraakgroepen aangemaakt en er zijn geen afspraakgroepen waarvoor je kunt inschrijven\",\"scheduler_explanation_teacher_paragraph_1\":\"Dit is de planner/de planningstool. Je kunt deze gebruiken om afspraakgroepen aan te maken zodat jouw cursisten zich hiervoor kunnen inschrijven.\",\"scheduler_explanation_teacher_paragraph_2\":\"Stel, dat je een schriftelijke tussentijdse opdracht met alle cursisten uit de cursus wilt bespreken. Je kunt dan een nieuwe afspraakgroep aanmaken, je kunt opgeven op welke dagen en tijdstippen je beschikbaar bent, je kunt de tijdstippen opsplitsen in blokken van 15 minuten en maximaal een cursist per tijdblok toelaten.\",\"scheduler_explanation_teacher_paragraph_3\":\"De cursisten ontvangen dan een bericht dat ze zich moeten inschrijven voor een van de opgegeven tijdblokken. De cursisten krijgen een combinatie van een kalender met de beschikbare tijdblokken te zien, zodat ze een voor hen geschikte tijd kunnen kiezen.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Nu beginnen door aan rechts op de toets \\\"Een afspraakgroep maken\\\" te klikken. *\",\"signed_up_for\":\"*%{title} afspraakgroep* waarvoor je bent aangemeld\",\"x_groups_have_signed_up\":\"%{n_participant_count} groepen hebben zich aangemeld\",\"x_people_have_signed_up\":\"%{n_participant_count} personen hebben zich aangemeld\"}},\"message_groups_who_1ba736f\":\"Een bericht versturen naar groepen die...\",\"message_students_who_369b8488\":\"Een bericht versturen naar cursisten die...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} appointment group* Je kunt je hiervoor **aanmelden** .\"},\"nn\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Slett\",\"edit\":\"Rediger\",\"location\":\"Plassering:\",\"manage\":\"Administrer avtalegruppa %{title}\",\"scheduler_explanation_student_header\":\"Du har ingen avtalegrupper du kan melde deg på\",\"scheduler_explanation_student_paragraph_1\":\"Faglærarar kan bruke dette verktøyet til å setje opp aktivitetar som studentane kan registrere seg på, t.d. kontortid, tidsluker for gruppepresentasjonar, o.a.\",\"scheduler_explanation_student_paragraph_2\":\"Akkurat no har ingen av faglærarane dine ledige avtaletider. Du vil få varsel om ein avtaletid blir ledig. \",\"scheduler_explanation_teacher_header\":\"Du har ikkje oppretta avtalegrupper og det finst ingen du kan melde deg på\",\"scheduler_explanation_teacher_paragraph_1\":\"Dette er planleggingsverktøyet. Du kan bruke det til å opprette avtalegrupper som studentane kan melde seg på.\",\"scheduler_explanation_teacher_paragraph_2\":\"La oss seie at du ynskjer å gå gjennom ei semesteroppgåve med kvar enkelt student på emnet. Du opprettar ei ny avtalegruppe, oppgir datoar og klokkeslett du er ledig, legg inn avtalebolkar på 15 minutt og tillet berre ein student per avtale.\",\"scheduler_explanation_teacher_paragraph_3\":\"Studentane får deretter ei fråsegn om at dei må melde seg på ein av dei spesifiserte avtalebolkane. Dei ser ein kalender med dei ledige avtalebolkane som er lagt over alle andre avtalar i kalenderen, slik at dei kan velje det tidspunktet som passar.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kom i gang no ved å klikke på knappen \\\"Opprett avtalegruppe\\\" til høgre. *\",\"signed_up_for\":\"Du har meldt deg på *avtalegruppa %{title}*\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupper har meldt seg på\",\"x_people_have_signed_up\":\"%{n_participant_count} personar har meldt seg på\"}},\"message_groups_who_1ba736f\":\"Send melding til grupper som...\",\"message_students_who_369b8488\":\"Send melding til studentar som...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} avtalegruppe* Du kan **melde deg** på.\"},\"pl\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Usuń\",\"edit\":\"Edytuj\",\"location\":\"Położenie:\",\"manage\":\"Zarządzaj spotkaniem grupowym %{title}\",\"scheduler_explanation_student_header\":\"Nie masz żadnych spotkań grupowych, na które możesz się zapisać\",\"scheduler_explanation_student_paragraph_1\":\"Nauczyciele mogą użyć tego narzędzia do planowania zajęć dla uczestników kursów, np. godzin pracy, przedziałów czasowych na prezentację grupowe itp.\",\"scheduler_explanation_student_paragraph_2\":\"Obecnie żaden z nauczycieli nie udostępnił żadnych przydziałów. Gdy przydziały zostaną udostępnione, zostaniesz powiadomiony.\",\"scheduler_explanation_teacher_header\":\"Nie utworzono żadnej grup spotkań, nie ma też żadnych, do których można się zapisać.\",\"scheduler_explanation_teacher_paragraph_1\":\"To jest narzędzie planowania. Możesz go używać do tworzenia spotkań grupowych, na które będą się mogli zapisywać uczestnicy.\",\"scheduler_explanation_teacher_paragraph_2\":\"Na przykład powiedzmy, że chcesz, aby każdy z uczestników kursu napisał pracę semestralną. Możesz utworzyć nową grupę spotkań, określić daty oraz czas dostępności. Podziel ten czas na 15 minutowe bloki i zezwól na jednego studenta w trakcie bloku.\",\"scheduler_explanation_teacher_paragraph_3\":\"Następnie uczestnicy zostaną powiadomieni i zobaczą, że powinni  zarejestrować się w jednym z podanych terminów. Będą one wyświetlane w kalendarzu z dostępnymi terminami, które będą nałożone na ich pozostałe kalendarze, aby mogli wybrać najbardziej dogodny dla nich termin.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Rozpocznij teraz, klikając przycisk \\\"Utwórz spotkanie grupowe\\\" po prawej stronie. *\",\"signed_up_for\":\"Zapisałeś się na *%{title} spotkanie grupowe*\",\"x_groups_have_signed_up\":\"%{n_participant_count} zgłosiły się nowe grupy\",\"x_people_have_signed_up\":\"%{n_participant_count} zgłosili się nowi ludzie\"}},\"message_groups_who_1ba736f\":\"Wyślij wiadomość do grup, które ...\",\"message_students_who_369b8488\":\"Wyślij wiadomość do uczestników, którzy...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} spotkanie grupowe* Możesz się **zapisać** na to wydarzenie.\"},\"pt\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Eliminar\",\"edit\":\"Editar\",\"location\":\"Localização:\",\"manage\":\"Gerir %{title} grupo de compromissos\",\"scheduler_explanation_student_header\":\"Não dispõe de nenhum grupo de compromissos para se inscrever\",\"scheduler_explanation_student_paragraph_1\":\"Os professores utilizam esta ferramenta para agendar coisas com os seus alunos para se registarem, isto é, horários, intervalos de tempo de apresentação em grupo, etc.\",\"scheduler_explanation_student_paragraph_2\":\"Actualmente, nenhum dos teus professores tem agendamentos disponíveis. Serás notificado se houverem horários de agendamentos disponíveis.\",\"scheduler_explanation_teacher_header\":\"Não criou nenhum grupo de compromissos e não existem grupos para os quais se possa inscrever\",\"scheduler_explanation_teacher_paragraph_1\":\"Esta é a Ferramenta de Agendamento. Pode utilizá-la para criar grupos de compromissos para os quais os seus alunos se possam inscrever.\",\"scheduler_explanation_teacher_paragraph_2\":\"Por exemplo, imagine que pretende analisar o trabalho de avaliação intercalar com cada um dos alunos inscritos na sua disciplina. Teria de criar um novo grupo de compromissos, especificar as datas e as horas em que se encontra disponível, dividir esse tempo em blocos de 15 minutos e permitir apenas um aluno por cada bloco de tempo.\",\"scheduler_explanation_teacher_paragraph_3\":\"Em seguida, os alunos seriam notificados e constatavam que teriam de se inscrever para obterem um dos blocos de tempo especificados. É-lhes apresentado um calendário com os blocos de tempo disponíveis colocado por cima de toda a informação constante dos seus outros calendários, para poderem escolher uma hora que lhes seja conveniente.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Comece já clicando no botão \\\"Criar um grupo de compromissos\\\" no lado direito. *\",\"signed_up_for\":\"*%{title} grupo de compromisso* Está inscrito para\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupos que se inscreveram\",\"x_people_have_signed_up\":\"%{n_participant_count} pessoas que se inscreveram\"}},\"message_groups_who_1ba736f\":\"Contactar grupos que...\",\"message_students_who_369b8488\":\"Enviar mensagem aos alunos que...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} grupo de compromisso* Pode **inscrever-se** nisto.\"},\"pt-BR\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Excluir\",\"edit\":\"Editar\",\"location\":\"Local:\",\"manage\":\"Gerenciar grupo de compromissos de %{title}\",\"scheduler_explanation_student_header\":\"Você não tem nenhum grupo de compromisso para se registrar\",\"scheduler_explanation_student_paragraph_1\":\"Os professores podem utilizar esta ferramenta para agendar coisas para os seus alunos se registrarem, como: horas de trabalho, intervalos de tempo para apresentação de grupo, etc.\",\"scheduler_explanation_student_paragraph_2\":\"Atualmente, nenhum professor disponibilizou quaisquer compromissos. Você será notificado se horários para compromissos ficarem disponíveis.\",\"scheduler_explanation_teacher_header\":\"Você não criou nenhum grupo de compromissos e não há nenhum para se registrar\",\"scheduler_explanation_teacher_paragraph_1\":\"Esta é o Agendador. Você pode usá-lo para criar grupos de compromisso para seus alunos se registrarem.\",\"scheduler_explanation_teacher_paragraph_2\":\"Por exemplo, digamos que você queira revisar os trabalhos de meio do período com cada um dos alunos no seu curso. Você criaria um novo grupo de compromisso, especificaria as datas e as horas que você tem disponíveis, dividiria aquele horário em blocos de 15 minutos e permitiria apenas um aluno por bloco.\",\"scheduler_explanation_teacher_paragraph_3\":\"Então, os alunos seriam notificados e veriam que precisam se registrar em um dos horários especificados. Eles veriam um calendário com os blocos especificados sobrepostos na parte superior de tudo em seus outros calendários para que possam escolher um horário que seja bom para eles.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Comece agora clicando no botão \\\"Criar um grupo de compromisso\\\" à direita. *\",\"signed_up_for\":\"*Grupo de compromissos de %{title}* no qual você está inscrito\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupos se inscreveram\",\"x_people_have_signed_up\":\"%{n_participant_count} pessoas se inscreveram\"}},\"message_groups_who_1ba736f\":\"Enviar mensagem aos grupos que...\",\"message_students_who_369b8488\":\"Enviar mensagem aos alunos que...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*Grupo de compromissos de %{title}* Você pode **registrar-se** nisso.\"},\"ru\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Удалить\",\"edit\":\"Редактировать\",\"location\":\"Местоположение:\",\"manage\":\"Управлять %{title} группы назначений\",\"scheduler_explanation_student_header\":\"У вас отсутствуют какие-либо групповые встречи для подписки\",\"scheduler_explanation_student_paragraph_1\":\"Преподаватели могут использовать данный инструмент для планирования мероприятий, на которые будут записываться учащиеся, например, учебных занятий, групповых презентаций и проч.\",\"scheduler_explanation_student_paragraph_2\":\"В настоящее время никто из ваших преподавателей не предоставил данных о наличии свободных временных интервалов для назначения встречи. Вас уведомят в случае появления свободного временного интервала для назначения встречи.\",\"scheduler_explanation_teacher_header\":\"Вы не создали ни одной групповой встречи, и не можете зарегистрироваться на участие в них\",\"scheduler_explanation_teacher_paragraph_1\":\"Это инструмент планирования. Вы можете использовать его для создания групповых встреч, на которые могут записаться ваши студенты\",\"scheduler_explanation_teacher_paragraph_2\":\"Предположим, что вы хотите пройтись по каждой экзаменационной работе студентов своего курса, сданной в середине семестра. Вам необходимо создать новую групповую встречу, указать дату и время, когда вы будете свободны, и запросить, чтобы система разделила этот период времени на 15-минутные блоки и допускала только одного студента на каждый блок времени.\",\"scheduler_explanation_teacher_paragraph_3\":\"Затем студенты получат уведомление и увидят, что они должны зарегистрироваться в одном из указанных блоков времени. Им будет показан календарь с имеющимися блоками времени, накладывающийся на их другие календари, чтобы они могли выбрать наиболее подходящее для себя время.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Начните прямо сейчас, нажав кнопку \\\"Создать групповую встречу\\\" справа. *\",\"signed_up_for\":\"*%{title} группа назначений* Вы подписались на\",\"x_groups_have_signed_up\":\"%{n_participant_count} подписавшиеся группы\",\"x_people_have_signed_up\":\"%{n_participant_count} подписавшиеся люди\"}},\"message_groups_who_1ba736f\":\"Сообщения групп, которые...\",\"message_students_who_369b8488\":\"Отправить сообщения учащимся, которые...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} группа назначений* Вы можете **подписаться** на\"},\"sl\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Odstrani\",\"edit\":\"Uredi\",\"location\":\"Mesto:\",\"manage\":\"Urejaj skupino sestanka %{title}.\",\"scheduler_explanation_student_header\":\"Nimate nobenih skupin sestankov, v katere bi se prijavili.\",\"scheduler_explanation_student_paragraph_1\":\"Izvajalci lahko s tem orodjem razporejajo zadeve, kot so npr. uradne ure, termini predstavitve skupine itd., da se lahko njihovi študenti prijavijo nanje.\",\"scheduler_explanation_student_paragraph_2\":\"Trenutno noben od izvajalcev ni dal na voljo nobenih sestankov. Ko bodo termini sestankov na voljo, boste o tem obveščeni.\",\"scheduler_explanation_teacher_header\":\"Niste ustvarili nobenih skupin sestankov in ni nobenih, na katere bi se lahko prijavili.\",\"scheduler_explanation_teacher_paragraph_1\":\"To je razporejevalnik. Lahko ga uporabljate za ustvarjanje skupin sestankov, da se študenti prijavijo nanje.\",\"scheduler_explanation_teacher_paragraph_2\":\"Recimo, da na primer želite z vsakim študentom pri vašem predmetu pregledati nalogo za pisni izdelek med semestrom. Ustvarili boste novo skupino sestankov, v njej navedli datume in čas, ko ste na voljo, določili, naj ta čas razdeli v 15-minutne bloke, in v enem časovnem bloku dovolili samo enega študenta.\",\"scheduler_explanation_teacher_paragraph_3\":\"Nato bodo študenti obveščeni in videli bodo, da se morajo prijaviti za enega od navedenih časovnih blokov. Prikazan jim bo koledar z razpoložljivimi časovnimi bloki, ki bodo v njihovih drugih koledarjih naloženi na vrh vsega ostalega, tako da bodo študenti lahko izbrali želeni čas.\",\"signed_up_for\":\"*%{title} skupina sestankov* Prijavljeni ste za\",\"x_groups_have_signed_up\":\"%{n_participant_count} skupin se je prijavilo\",\"x_people_have_signed_up\":\"%{n_participant_count} sodelujočih se je prijavilo\"}},\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} skupina sestankov* Na to se lahko **prijavite**.\"},\"sv\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Ta bort\",\"edit\":\"Redigera\",\"location\":\"Plats:\",\"manage\":\"Hantera %{title} mötesgrupp\",\"scheduler_explanation_student_header\":\"Du har inga mötesgrupper att registrera dig för\",\"scheduler_explanation_student_paragraph_1\":\"Lärare kan använda detta verktyg för att schemalägga saker som deras studenter kan anmäla sig till, t.ex. kontorstider, tidsluckor för gruppresentationer osv.\",\"scheduler_explanation_student_paragraph_2\":\"För närvarande har ingen av dina lärare gjort några möten tillgängliga. Du kommer att informeras om mötestider blir tillgängliga.\",\"scheduler_explanation_teacher_header\":\"Du har inte skapat några mötesgrupper och det finns inga du kan registrera dig för\",\"scheduler_explanation_teacher_paragraph_1\":\"Det här är schemabokningsverktyget. Du kan använda det för att skapa mötesgrupper som dina studenter kan registrera sig för.\",\"scheduler_explanation_teacher_paragraph_2\":\"Till exempel, låt oss säga att du vill gå igenom en uppsatsuppgift i mitten på  terminen med alla studenter i din kurs. Du kan då skapa en mötesgrupp, specificera tider och datum då du är tillgänglig, be verktyget dela upp den tiden i 15-minutersblock och tillåta en student per block.\",\"scheduler_explanation_teacher_paragraph_3\":\"Därefter meddelas studenterna och  ser att de måste boka in sig för ett av de specificerade blocken. De kommer se en kalender med de tillgängliga tidsblocken ovanför allt på sina andra kalendrar så att de kan välja en tid som passar dem.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kom igång genom att klicka på knappen \\\"Skapa en mötesgrupp\\\" till höger. *\",\"signed_up_for\":\"*%{title} mötesgruppen* som du registrerat dig för\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupper har anmält sig\",\"x_people_have_signed_up\":\"%{n_participant_count} människor har anmält sig\"}},\"message_groups_who_1ba736f\":\"Meddela grupper som ...\",\"message_students_who_369b8488\":\"Meddela studenter som...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} mötesgrupp* Du kan **registrera dig** för den här.\"},\"sv-x-k12\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Ta bort\",\"edit\":\"Redigera\",\"location\":\"Plats:\",\"manage\":\"Hantera %{title} mötesgrupp\",\"scheduler_explanation_student_header\":\"Du har inga mötesgrupper att registrera dig för\",\"scheduler_explanation_student_paragraph_1\":\"Lärare kan använda detta verktyg för att schemalägga saker som deras elever kan anmäla sig till, t.ex. kontorstider, tidsluckor för gruppresentationer osv.\",\"scheduler_explanation_student_paragraph_2\":\"För närvarande har ingen av dina lärare gjort några möten tillgängliga. Du kommer att informeras om mötestider blir tillgängliga.\",\"scheduler_explanation_teacher_header\":\"Du har inte skapat några mötesgrupper och det finns inga du kan registrera dig för\",\"scheduler_explanation_teacher_paragraph_1\":\"Det här är schemabokningsverktyget. Du kan använda det för att skapa mötesgrupper som dina elever kan registrera sig för.\",\"scheduler_explanation_teacher_paragraph_2\":\"Till exempel, låt oss säga att du vill gå igenom en uppsatsuppgift i mitten på  terminen med alla elever i din kurs. Du kan då skapa en mötesgrupp, specificera tider och datum då du är tillgänglig, be verktyget dela upp den tiden i 15-minutersblock och tillåta en elev per block.\",\"scheduler_explanation_teacher_paragraph_3\":\"Därefter meddelas eleverna och  ser att de måste boka in sig för ett av de specificerade blocken. De kommer se en kalender med de tillgängliga tidsblocken ovanför allt på sina andra kalendrar så att de kan välja en tid som passar dem.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Kom igång genom att klicka på knappen \\\"Skapa en mötesgrupp\\\" till höger. *\",\"signed_up_for\":\"*%{title} mötesgruppen* som du registrerat dig för\",\"x_groups_have_signed_up\":\"%{n_participant_count} grupper har anmält sig\",\"x_people_have_signed_up\":\"%{n_participant_count} människor har anmält sig\"}},\"message_groups_who_1ba736f\":\"Meddela grupper som ...\",\"message_students_who_369b8488\":\"Meddela elever som ...\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} mötesgrupp* Du kan **registrera dig** för den här.\"},\"tr\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Sil\",\"edit\":\"Düzenle\",\"location\":\"Konum:\",\"manage\":\"%{title} randevu grubunu yönet\",\"scheduler_explanation_student_header\":\"Kaydolacağınız herhangi bir görüşme grubu yok\",\"scheduler_explanation_student_paragraph_1\":\"\\\"Ofis saatleri\\\",  \\\"grup sunuları zaman aralıkları\\\" vb gibi öğrencilerin kayıt yaptıracakları olayları zamanlamak için öğretmenler  bu aracı kullanabilir.\",\"scheduler_explanation_student_paragraph_2\":\"Şu anda öğretmenlerinizden hiç biri geçerli bir randevu ayarlamadı. Katılınabilecek bir randevu aralığı olduğunda bilgilendiriliceksiniz.\",\"scheduler_explanation_teacher_header\":\"Randevu grubu oluşturmadınız ve katılabileceğiniz bir grup bulunmamaktadır\",\"scheduler_explanation_teacher_paragraph_1\":\"Bu Program Yapma Aracıdır. Bu aracı, öğrencilerinizin katılmak için kayıt yaptırabilecekleri randevu grupları oluşturmak için kullanabilirsiniz.\",\"scheduler_explanation_teacher_paragraph_2\":\"Örnek olarak diyelim ki dersinizi alan her bir öğrenci ile ara sınav ödevini görüşmek istediniz. Aracı kullanarak yeni bir randevu grubu oluşturabilir, sizin için uygun olan tarih ve zamanı belirtebilir, araca bu süreyi 15'er dakikalık bloklara böldürüp her zaman bloğu için bir öğrenci ayarlamasını sağlayabilirsiniz.\",\"scheduler_explanation_teacher_paragraph_3\":\"Daha sonra öğrenciler bilgilendirilecek ve belirlenmiş zaman bloklarından birine kayıt yaptırmaları gerektiğini görecekler. Öğrenciler kendilerine uygun bir zamanı belirleyebilmeleri için diğer takvimlerin de üstünde bir takvim üzerinde zaman bloklarını görecekler.\",\"signed_up_for\":\"Kayıtlı olduğunuz *%{title} randevu grubu*\",\"x_groups_have_signed_up\":\"%{n_participant_count} grup kayıt oldu\",\"x_people_have_signed_up\":\"%{n_participant_count} kişi kayıt oldu\"}},\"message_groups_who_1ba736f\":\"Şu Gruplara Mesaj Gönder...\",\"message_students_who_369b8488\":\"... Yapan Öğrencileri Göster\"},\"uk\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"Видалити\",\"edit\":\"Редагувати\",\"location\":\"Місцезнаходження:\",\"manage\":\"Керувати %{title} призначеною групою\",\"scheduler_explanation_student_header\":\"Ви не маєте будь-яких груп призначення, щоб підписатися на\",\"scheduler_explanation_student_paragraph_1\":\"Вчителі можуть використовувати цей інструмент, щоб запланувати речі для своїх студентів, щоб зареєструвати, наприклад, робочий час, тимчасові інтервали презентації групи і т.д.\",\"scheduler_explanation_student_paragraph_2\":\"В даний час жоден з ваших вчителів не зробив жодних призначень доступними. Вам буде повідомлено, якщо призначення слотів стануть доступними.\",\"scheduler_explanation_teacher_header\":\"Ви ще не створилизустріч групи, і немає ні одної, на яку ви можете підписатися \",\"scheduler_explanation_teacher_paragraph_1\":\"Це інструмент Планувальник. Ви можете використовувати його для створення зустрічі груп для ваших студентів, щоб підписатися на.\",\"scheduler_explanation_teacher_paragraph_2\":\"Наприклад, припустимо, ви хочете, щоб перейти на поступки щодо середнього терміну бланкового тесту для кожного студента в курсі. Ви маєте створити нову зустріч групи, вказати доступні дату і час, повідомити про розподіл цього часу на 15 хвилинні блоки і дозволити одному студенту займати одночасно тільки один блок.\",\"scheduler_explanation_teacher_paragraph_3\":\"Потім студенти будуть отримувати повідомлення і бачити, що вони повинні підписатися на один із вказаних блоків часу. Ім буде показаний календар з наявними блоками часу. який накладається на всі їх інші календарі, таким чином вони можуть вибрати час, придатний для них.\",\"scheduler_explanation_teacher_paragraph_4\":\"* Почніть прямо зараз, натиснувши на кнопку \\\"Створити зустріч групи\\\" праворуч.*\",\"signed_up_for\":\"*%{title} призначення групи* Ви підписали на\",\"x_groups_have_signed_up\":\"%{n_participant_count} груп підписалися на\",\"x_people_have_signed_up\":\"%{n_participant_count} людей підписалися на\"}}},\"zh-Hans\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"删除\",\"edit\":\"编辑\",\"location\":\"位置:\",\"manage\":\"管理 %{title} 预约小组\",\"scheduler_explanation_student_header\":\"您没有任何要注册的预约组\",\"scheduler_explanation_student_paragraph_1\":\"教师可以利用该工具安排事项供学生注册，例如师生互动时间、小组演讲时段，等等。\",\"scheduler_explanation_student_paragraph_2\":\"目前，你的教师没有布置任何预约。如果有可用的预约位置，您将收到通知。\",\"scheduler_explanation_teacher_header\":\"您尚未创建任何预约组，也没有您可注册的预约\",\"scheduler_explanation_teacher_paragraph_1\":\"这是评价量规工具。 您可以利用它为学生创建要注册的预约组。\",\"scheduler_explanation_teacher_paragraph_2\":\"比方说，您想与参加您课程的每个学生单独讨论期中论文预约。您可以新建一个预约组， 指定对您方便的日期和时间，并将该时间分成多个时间块［每个块 15 分钟］，每个时间块仅允许一个学生参与。\",\"scheduler_explanation_teacher_paragraph_3\":\"然后，学生将收到通知，并了解需要注册一个指定的时间块。在随后显示的日历中，可用时间块覆盖在日历的上部，这样学生就可选择一个合适的时间。\",\"scheduler_explanation_teacher_paragraph_4\":\"* 单击右侧的“创建预约组”按钮，即可开始创建。*\",\"signed_up_for\":\"您所登记的*%{title} 预约小组*\",\"x_groups_have_signed_up\":\"%{n_participant_count} 已经登记的小组\",\"x_people_have_signed_up\":\"%{n_participant_count} 已经登记的人员\"}},\"message_groups_who_1ba736f\":\"发送消息到小组...\",\"message_students_who_369b8488\":\"给……学生发送信息\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title}的预约小组* 您可为此而 **登记** 。\"},\"zh-Hant\":{\"calendar\":{\"appointment_group_list\":{\"delete\":\"刪除\",\"edit\":\"編輯\",\"location\":\"位置：\",\"manage\":\"管理 %{title} 預約群組\",\"scheduler_explanation_student_header\":\"您沒有任何要報名參加的預約群組\",\"scheduler_explanation_student_paragraph_1\":\"老師可使用此工具安排學生登記，例如工作時間，小組討論時段等。\",\"scheduler_explanation_student_paragraph_2\":\"所有老師最近都沒有可供預約的時間。當有可預約時段時，您將會收到通知。\",\"scheduler_explanation_teacher_header\":\"您尚未創建任何預約群組，並且沒有您可以報名參加預約\",\"scheduler_explanation_teacher_paragraph_1\":\"這是排程器工具。您可以用其創建您的學生可報名參加預約群組。\",\"scheduler_explanation_teacher_paragraph_2\":\"例如，假設您想瀏覽您課程中每個學生的期中論文作業。您將創建一個新的預約群組，指定您有空的日期和時間，讓其將該時間分成 15 分鐘的時間段，每個時間段只允許有一名學生預約。\",\"scheduler_explanation_teacher_paragraph_3\":\"然後，學生們會得到通知，看看他們需要報名參加的指定的時間段。他們的日曆上將會顯示可用的時間段，並且覆蓋他們的日曆上的其他一切事項，以便他們可以選擇一個他們可以利用的時間。\",\"scheduler_explanation_teacher_paragraph_4\":\"* 按一下右邊的『創建預約群組』按鈕，將會馬上開始。*\",\"signed_up_for\":\"您已報名參加 *%{title} 預約群組*\",\"x_groups_have_signed_up\":\"%{n_participant_count} 組已登記\",\"x_people_have_signed_up\":\"%{n_participant_count} 人已登記\"}},\"message_groups_who_1ba736f\":\"通知以下群組…\",\"message_students_who_369b8488\":\"傳送訊息給以下學生…\",\"title_appointment_group_you_can_sign_up_for_this_1c254712\":\"*%{title} 預約群組* 您可以 **報名** 加入其中。\"}}"));
